const getRandomItems = (movies: Movie[]) => {
  const result = [];
  const taken = new Set();

  while (result.length < 10 && result.length < movies.length) {
    const randomIndex = Math.floor(Math.random() * movies.length);
    if (!taken.has(randomIndex)) {
      result.push(movies[randomIndex]);
      taken.add(randomIndex);
    }
  }

  return result;
};

const recommendedVideos = getRandomItems(
  movies.filter((movie) => !movie.isTrending)
);

async function isAuthenticated({
  cookies,
}: {
  cookies: Record<string, string>;
}) {
  if (!cookies.authToken) {
    throw new HttpResponse(null, { status: 403 });
  }
}

export const handlers = [
  http.get("/api/user", async ({ cookies }) => {
    await isAuthenticated({ cookies });

    const user = users.find((user) => user.token === cookies.authToken);
    if (!user) {
      return new HttpResponse(null, { status: 403 });
    }

    const { password: _, ...userData } = user;

    return HttpResponse.json(userData);
  }),
  http.get("/api/logout", () => {
    return new HttpResponse(null, {
      status: 201,
      headers: {
        "Set-Cookie": `authToken=; Max-Age=-99999999;`,
      },
    });
  }),
  http.post("/api/login", async ({ request }) => {
    const data = await request.formData();

    const username = data.get("username");
    const password = data.get("password");

    const user = users.find((user) => user.username === username);
    if (!user || user.password !== password) {
      throw new HttpResponse("Invalid username or password", { status: 401 });
    }

    const { password: _, ...userData } = user;

    return HttpResponse.json(userData, {
      headers: {
        "Set-Cookie": `authToken=${user.token}`,
      },
    });
  }),
  http.get("/api/movies", async ({ cookies, request }) => {
    await isAuthenticated({ cookies });

    const url = new URL(request.url);

    const genre = url.searchParams.get("genre");
    const keyword = url.searchParams.get("keyword")?.toLowerCase();

    let listOfVideos = movies
      .filter((movie) => {
        if (genre) {
          return movie.genre === genre;
        }
        if (keyword) {
          return (
            movie.title.toLowerCase().includes(keyword) ||
            movie.genre.toLowerCase().includes(keyword) ||
            movie.actors.map((actor) => actor.toLowerCase()).includes(keyword)
          );
        }
        return true;
      })
      .map((movie) => {
        movie.bookmarked = !!bookmarkedIds.includes(movie.id);

        return movie;
      });

    return HttpResponse.json(listOfVideos, { status: 200 });
  }),
  http.get("/api/movies/:id", async ({ cookies, params }) => {
    await isAuthenticated({ cookies });

    const { id } = params;
    if (!id || typeof id !== "string") {
      return new HttpResponse(null, { status: 403 });
    }

    const foundMovie = movies.find((movie) => movie.id === id);
    if (!foundMovie) {
      return new HttpResponse(null, { status: 404 });
    }

    return HttpResponse.json(foundMovie, { status: 200 });
  }),
  http.post("/api/bookmarks/:id", async ({ cookies, params }) => {
    await isAuthenticated({ cookies });

    const { id } = params;
    if (!id || typeof id !== "string") {
      return new HttpResponse(null, { status: 403 });
    }

    bookmarkedIds.push(id as unknown as string);

    return new HttpResponse(null, { status: 200 });
  }),
  http.delete("/api/bookmarks/:id", async ({ cookies, params }) => {
    await isAuthenticated({ cookies });

    const { id } = params;
    if (!id || typeof id !== "string") {
      return new HttpResponse(null, { status: 403 });
    }

    bookmarkedIds = bookmarkedIds.filter((bookmarkedId) => {
      return (id as unknown as string) !== bookmarkedId;
    });

    return new HttpResponse(null, { status: 200 });
  }),
  http.get("/api/bookmarks", async ({ cookies }) => {
    await isAuthenticated({ cookies });

    const bookmarkedMovies = movies.filter((movie) =>
      bookmarkedIds.includes(movie.id)
    );

    return HttpResponse.json(bookmarkedMovies, { status: 200 });
  }),
  http.get("/api/recommended", async ({ cookies }) => {
    await isAuthenticated({ cookies });

    return HttpResponse.json(recommendedVideos, { status: 200 });
  }),
  http.get("/api/categories", async ({ cookies }) => {
    await isAuthenticated({ cookies });

    const genres = [...new Set(movies.map((video) => video.genre))];

    return HttpResponse.json(genres, { status: 200 });
  }),
];
